import {createNativeStackNavigator} from '@react-navigation/native-stack';
import {ApplicationProvider, IconRegistry} from '@ui-kitten/components';
import {PaperProvider} from 'react-native-paper';
import * as eva from '@eva-design/eva';
import {EvaIconsPack} from '@ui-kitten/eva-icons';
import {default as theme} from '../../custom-theme.json'; // <-- Import app theme
import {default as mapping} from '../../mapping.json'; // <-- Import app mapping
import Toast from 'react-native-toast-message';
import NavigationNameAccount from '../constants/NavigationNameAccount';
import AccountScreen from '../app/account';
import InformationScreen from '../app/account/infomation';
import ChangePasswordScreen from '../app/account/change-password';
import React, {useEffect, useState} from 'react';
import PasswordSetting from '../app/account/settings/password';
import {ActivityIndicator, View} from 'react-native';
import {Color} from '../constants';
import toastConfig from '../utils/ToastConfig';
import {useAccountSettingService} from '../hooks/apiHooks/useAccountInfoServices';
import {ServiceIdApp} from '../model/enum/common.enum';
import {useZutandStore} from '../zutand/store';
const Stack = createNativeStackNavigator();

const AccountNavigation = (props: any) => {
  const {setListMenuViewEAccount} = useZutandStore();
  const [isLoadInitData, setIsLoadInitData] = useState(true);
  const SettingAccountServices = useAccountSettingService();
  
  const getMenuViewEAccount = async () => {
    const listMenuViewEAccount = await SettingAccountServices.getMenuView(
      ServiceIdApp.EACCOUNT,
    );
    setListMenuViewEAccount(listMenuViewEAccount);
  };

  useEffect(() => {
    getMenuViewEAccount();
    setIsLoadInitData(false);
  }, []);

  if (isLoadInitData) {
    return (
      <View
        style={{
          flex: 1,
          justifyContent: 'center',
          alignItems: 'center',
        }}>
        <ActivityIndicator size={'large'} color={Color.BgPrimary} />
      </View>
    );
  }

  return (
    <>
      <IconRegistry icons={[EvaIconsPack]} />
      <ApplicationProvider
        customMapping={mapping as any}
        {...eva}
        theme={{...eva.light, ...theme}}>
        <PaperProvider>
          <Stack.Navigator
            // initialRouteName={NavigationNameAccount.ChangePasswordScreen}
            screenOptions={{
              headerShown: false,
            }}>
            <Stack.Screen
              initialParams={props}
              name={NavigationNameAccount.AccountScreen}
              component={AccountScreen}
            />
            <Stack.Screen
              name={NavigationNameAccount.InformationScreen}
              component={InformationScreen}
            />
            <Stack.Screen
              name={NavigationNameAccount.ChangePasswordScreen}
              component={ChangePasswordScreen}
            />
            <Stack.Screen
              name={NavigationNameAccount.PasswordSetting}
              component={PasswordSetting}
            />
          </Stack.Navigator>
          <Toast config={toastConfig} />
        </PaperProvider>
      </ApplicationProvider>
    </>
  );
};

export default AccountNavigation;
