import {isNumber} from 'lodash';
import {IConfigPassword} from '../../interface';

export interface IAttrs {
  type: string;
  validators: any[];
  value: string;
}

export const handleInitialCounter = (
  type?: IConfigPassword,
  value?: string | number,
  value2?: string | number,
) => {
  const number = isNumber(value) ? value : parseFloat(value ?? '4');
  const number2 = isNumber(value2) ? value2 : parseFloat(value2 ?? '4');
  switch (type) {
    case IConfigPassword.complexPassword:
      return [{min: 4, max: 100, value: number}];
    case IConfigPassword.timeResetExpired:
      return [
        {min: 7, max: 20, value: number},
        {min: 30, max: 365, value: number2},
      ];
    case IConfigPassword.checkPasswordDuplicate:
      return [{min: 3, max: 10, value: number}];
    case IConfigPassword.lockingAccountWrongPassword:
      return [{min: 3, max: 10, value: number}];
    default:
      return [{min: 4, max: 100, value: number}];
  }
};

export const mappingAttributeTypes = (type?: IConfigPassword) => {
  switch (type) {
    case IConfigPassword.complexPassword:
      return ['min-length'];
    case IConfigPassword.timeResetExpired:
      return ['notifyAfterDay', 'resetAfterDay'];
    default:
      return ['amount'];
  }
};
