export interface IPasswordConfig {
  id: number;
  type: string;
  configType: string;
  attrs: any;
  status: boolean;
  custId: number;
  orgIn: string;
  createdDate: string;
  createdBy: string;
  lastModifiedDate: string;
  lastModifiedBy: string;
}

export enum IConfigPassword {
  /**  phc tp mt khu */
  complexPassword = 'complexPassword',
  /** Thi hn ht mt khu */
  timeResetExpired = 'timeResetExpired',
  /** Cu hnh khng trng mt khu */
  checkPasswordDuplicate = 'checkPasswordDuplicate',
  /** Cu hnh nhp sai mt khu */
  lockingAccountWrongPassword = 'lockingAccountWrongPassword',
}

export const LIST_CONFIG_PASSWORD = [
  {
    label: 'GS.passwordConfig.configComplexPassword',
    type: IConfigPassword.complexPassword,
  },
  {
    label: 'GS.passwordConfig.configTimeResetExpired',
    type: IConfigPassword.timeResetExpired,
  },
  {
    label: 'GS.passwordConfig.configCheckPasswordDuplicate',
    type: IConfigPassword.checkPasswordDuplicate,
  },
  {
    label: 'GS.passwordConfig.configLockingAccountWrongPassword',
    type: IConfigPassword.lockingAccountWrongPassword,
  },
];

export const isValueInEnum = (
  value: string,
  enumObject: typeof IConfigPassword,
): boolean => {
  return Object.values(enumObject).includes(value as IConfigPassword);
};
