import React, {useMemo, useState} from 'react';
import {useTranslation} from 'react-i18next';
import {
  KeyboardAvoidingView,
  Modal,
  TouchableOpacity,
  View,
} from 'react-native';
import {useSafeAreaInsets} from 'react-native-safe-area-context';
import {handleInitialCounter, IAttrs, mappingAttributeTypes} from './helper';
import styles from './styles';
import {
  IConfigPassword,
  IPasswordConfig,
  LIST_CONFIG_PASSWORD,
} from '../../interface';
import {useAppDispatch} from '../../../../../../hooks/useRedux';
import TextCM from '../../../../../components/Text';
import ButtonCM from '../../../../../components/Button';
import {Icon} from '@ui-kitten/components';
import useCounter from '../../../../../../hooks/useCounter';
import CounterItem from '../CounterItem';

type ModalConfigPasswordProp = {
  onClose: () => void;
  isVisible: boolean;
  data?: IPasswordConfig;
  type?: IConfigPassword;
  onSubmit: (type: IConfigPassword, data: any) => void;
};

const ModalConfigPassword = ({
  data,
  onClose,
  isVisible,
  type,
  onSubmit,
}: ModalConfigPasswordProp) => {
  const {t} = useTranslation();
  const insets = useSafeAreaInsets();

  const dispatch = useAppDispatch();

  const [attribute, setAttribute] = useState<any>(data?.attrs);

  const attributeTypeArr = useMemo(() => {
    return mappingAttributeTypes(type);
  }, [type]);

  const counterArr = useMemo(() => {
    return handleInitialCounter(
      type,
      data?.attrs?.[attributeTypeArr[0]]?.value ?? 0,
      data?.attrs?.[attributeTypeArr[1]]?.value ?? 0,
    );
  }, [data]);

  const [value, increase, decrease, setCountData] = useCounter({
    initialValue: counterArr[0].value ?? 0,
    min: counterArr[0].min,
    max: counterArr[0].max,
  });

  const [valueSecond, increaseSecond, decreaseSecond, setCountDataSecond] =
    useCounter({
      initialValue: counterArr?.[1]?.value ?? 0,
      min: counterArr?.[1]?.min,
      max: counterArr?.[1]?.max,
    });

  const handleOnsubmit = () => {
    if (attributeTypeArr.length > 1) {
      onSubmit(type ?? IConfigPassword.complexPassword, {
        ...data,
        attrs: {
          ...attribute,
          [attributeTypeArr[0]]: {
            ...attribute?.[attributeTypeArr[0]],
            value: value,
          },
          [attributeTypeArr[1]]: {
            ...attribute?.[attributeTypeArr[1]],
            value: valueSecond,
          },
        },
      });
    } else {
      onSubmit(type ?? IConfigPassword.complexPassword, {
        ...data,
        attrs: {
          ...attribute,
          [attributeTypeArr[0]]: {
            ...attribute?.[attributeTypeArr[0]],
            value: value,
          },
        },
      });
    }
  };

  const onToggleSelected = (attrSelect: IAttrs, attrType: string) => () => {
    if (attrSelect.type === 'Boolean') {
      attrSelect.value = attrSelect.value === 'true' ? 'false' : 'true';
      setAttribute({...attribute, [attrType]: attrSelect});
    }
  };

  const renderByType = () => {
    switch (type) {
      case IConfigPassword.complexPassword:
        return (
          <View style={styles.content}>
            <CounterItem
              label={t('GS.passwordConfig.minimumLengthTitle')}
              attention={t('GS.passwordConfig.minimumLengthCharacters', {
                min: counterArr[0].min,
                max: counterArr[0].max,
              })}
              value={value}
              onPressMinus={decrease}
              onPressPlus={increase}
              onPressSetData={setCountData}
              min={counterArr[0].min}
              max={counterArr[0].max}
            />
            <ItemChoose
              data={attribute?.['digit']}
              onToggleSelected={onToggleSelected(attribute?.['digit'], 'digit')}
              label={t('GS.passwordConfig.atleastDigit')}
            />
            <ItemChoose
              data={attribute?.['lowercase-char']}
              onToggleSelected={onToggleSelected(
                attribute?.['lowercase-char'],
                'lowercase-char',
              )}
              label={t('GS.passwordConfig.atleastLowercase')}
            />
            <ItemChoose
              data={attribute?.['uppercase-char']}
              onToggleSelected={onToggleSelected(
                attribute?.['uppercase-char'],
                'uppercase-char',
              )}
              label={t('GS.passwordConfig.atleastUppercase')}
            />
            <ItemChoose
              data={attribute?.['special-char']}
              onToggleSelected={onToggleSelected(
                attribute?.['special-char'],
                'special-char',
              )}
              label={t('GS.passwordConfig.atleastSpecialCharacter')}
            />
          </View>
        );
      case IConfigPassword.timeResetExpired:
        return (
          <View style={styles.content}>
            <CounterItem
              label={t('GS.passwordConfig.notifyAfterDayLabel')}
              attention={t('GS.passwordConfig.notifyAterDayAttention', {
                min: counterArr[0].min,
                max: counterArr[0].max,
              })}
              value={value}
              onPressMinus={decrease}
              onPressPlus={increase}
              onPressSetData={setCountData}
              min={counterArr[0].min}
              max={counterArr[0].max}
            />
            <CounterItem
              label={t('GS.passwordConfig.resetAfterDayLabel')}
              attention={t('GS.passwordConfig.resetAfterDayAttention', {
                min: counterArr?.[1]?.min,
                max: counterArr?.[1]?.max,
              })}
              value={valueSecond}
              onPressMinus={decreaseSecond}
              onPressPlus={increaseSecond}
              onPressSetData={setCountDataSecond}
              min={counterArr?.[1]?.min}
              max={counterArr?.[1]?.max}
            />
          </View>
        );
      case IConfigPassword.checkPasswordDuplicate:
        return (
          <CounterItem
            label={t('GS.passwordConfig.duplicatePasswordLabel')}
            attention={t('GS.passwordConfig.duplicatePasswordAttention', {
              min: counterArr[0].min,
              max: counterArr[0].max,
            })}
            value={value}
            onPressMinus={decrease}
            onPressPlus={increase}
            onPressSetData={setCountData}
            min={counterArr[0].min}
            max={counterArr[0].max}
          />
        );
      case IConfigPassword.lockingAccountWrongPassword:
        return (
          <CounterItem
            label={t('GS.passwordConfig.lockAccountLabel')}
            attention={t('GS.passwordConfig.lockAccountAttention', {
              min: counterArr[0].min,
              max: counterArr[0].max,
            })}
            value={value}
            onPressMinus={decrease}
            onPressPlus={increase}
            onPressSetData={setCountData}
            min={counterArr[0].min}
            max={counterArr[0].max}
          />
        );
      default:
        return null;
    }
  };

  return (
    <Modal animationType="fade" transparent visible={isVisible}>
      <KeyboardAvoidingView
        style={{flex: 1}}
        behavior={'padding'}
        keyboardVerticalOffset={-insets.bottom}>
        <View style={styles.ctnContainer}>
          <View style={styles.ctnTop} onTouchEnd={onClose} />
          <View style={[styles.ctnContent, {paddingBottom: insets.bottom}]}>
            <View style={styles.viewHeader}>
              <TouchableOpacity onPress={onClose}>
                <Icon name="chevron-left" width={14} height={16} />
              </TouchableOpacity>
              <TextCM style={styles.txtTitle}>{t('GS.config')}</TextCM>
              <View />
            </View>
            <View style={styles.viewBody}>
              <TextCM>
                {LIST_CONFIG_PASSWORD.find(i => i.type === type)?.label
                  ? t(
                      LIST_CONFIG_PASSWORD.find(i => i.type === type)
                        ?.label as string,
                    )
                  : ''}
              </TextCM>
              {renderByType()}
              <ButtonCM label={t('CM.save')} onPress={handleOnsubmit} />
            </View>
          </View>
        </View>
      </KeyboardAvoidingView>
    </Modal>
  );
};

interface ItemChooseProps {
  data: IAttrs;
  onToggleSelected: () => void;
  label: string;
}

const ItemChoose = ({data, onToggleSelected, label}: ItemChooseProps) => {
  return (
    <TouchableOpacity style={styles.viewItem} onPress={onToggleSelected}>
      {data?.value === 'true' ? (
        <Icon fill="#943cdd" name="checkmark-square-2" width={24} height={24} />
      ) : (
        <Icon fill="#a9b8d3" name="square" width={24} height={24} />
      )}
      <TextCM>{label}</TextCM>
    </TouchableOpacity>
  );
};

export default ModalConfigPassword;
