import React, {useRef, useState} from 'react';
import {useTranslation} from 'react-i18next';
import {Alert, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import TextFieldCM from '../../../../../components/TextField';
import ButtonCM from '../../../../../components/Button';
import TextCM from '../../../../../components/Text';
import BottomSheetCM from '../../../../../components/BottomSheet';
import {Icon} from '@ui-kitten/components';

interface Props {
  onPressMinus: () => void;
  onPressPlus: () => void;
  onPressSetData: (data: number) => void;
  value: number;
  label?: string;
  attention?: string;
  min: number;
  max: number;
}

const CounterItem = ({
  onPressMinus,
  value,
  onPressPlus,
  label,
  attention,
  min,
  max,
  onPressSetData,
}: Props) => {
  const {t} = useTranslation();
  const timerAddOne = useRef<any>(null);
  const timerMinusOne = useRef<any>(null);
  const [isVisible, setIsVisiable] = useState<boolean>(false);
  const [valueInput, setValueInput] = useState<string>(value.toString());

  const addOne = () => {
    onPressPlus();
    if (timerAddOne.current) clearTimeout(timerAddOne.current!);
    timerAddOne.current = setTimeout(addOne, 100);
  };

  const minusOne = () => {
    onPressMinus();
    if (timerAddOne.current) clearTimeout(timerAddOne.current!);
    timerAddOne.current = setTimeout(minusOne, 100);
  };

  const stopTimer = () => {
    clearTimeout(timerAddOne.current);
    clearTimeout(timerMinusOne.current);
  };

  const handleSubMitValueSelect = () => {
    const num = parseInt(valueInput);
    if (isNaN(num) || num < min || num > max) {
      Alert.alert(t('CM.InvalidNumber', {min, max}));
      return;
    }
    onPressSetData(num);
    setIsVisiable(false);
  };

  const renderItem = () => {
    return (
      <View style={{rowGap: 10}}>
        <TextFieldCM
          keyboardType="numeric"
          value={valueInput}
          onChangeText={setValueInput}
          label={t('CM.typeText')}
        />
        <ButtonCM label={t('CM.save')} onPress={handleSubMitValueSelect} />
      </View>
    );
  };

  return (
    <>
      <View style={styles.ctn}>
        {label && <TextCM style={styles.txtLabel}>{label}</TextCM>}
        <View style={styles.item}>
          <TouchableOpacity onPressIn={minusOne} onPressOut={stopTimer}>
            {value === min ? (
              <Icon name="minus" fill={'grey'} width={24} height={24} />
            ) : (
              <Icon name="minus" fill={'red'} width={24} height={24} />
            )}
          </TouchableOpacity>
          <TouchableOpacity hitSlop={15} onPress={() => setIsVisiable(true)}>
            <TextCM>{value}</TextCM>
          </TouchableOpacity>
          <TouchableOpacity onPressIn={addOne} onPressOut={stopTimer}>
            {value === max ? (
              <Icon name="plus" fill={'grey'} width={24} height={24} />
            ) : (
              <Icon name="plus" fill={'blue'} width={24} height={24} />
            )}
          </TouchableOpacity>
        </View>
        {attention && <TextCM style={styles.txtAttention}>{attention}</TextCM>}
      </View>

      <BottomSheetCM
        title={t('CM.typeText')}
        isVisible={isVisible}
        onClose={() => {
          setIsVisiable(false);
          setValueInput(value.toString());
        }}
        renderContent={renderItem()}
        withKeyboardAvoidingView={true}
      />
    </>
  );
};

export default CounterItem;
