import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import {
  ActivityIndicator,
  Image,
  ScrollView,
  TouchableOpacity,
  View,
} from 'react-native';
import TextCM from '../../components/Text';
import {useAppSelector} from '../../../hooks/useRedux';
import {IconCamera} from '../../../assets/images';
import React, {useCallback, useEffect, useState} from 'react';
import {Icon} from '@ui-kitten/components';
import {CommonUtils, DateUtils} from '../../../utils';
import styles from './styles';
import BottomSheetCM from '../../components/BottomSheet';
import AvatarModal from './components/Avatar';
import ModalFullScreen from '../../components/ModalFullScreen';
import ChangePhone from './components/ChangePhone';
import {getConfig} from '../../../services/httpClient';
import {AccountSystem} from '../../../model/enum/common.enum';
import {useAccountInfoServices} from '../../../hooks/apiHooks/useAccountInfoServices';
import {
  OrgLegalRepresentative,
  OrgProfileAuthenticated,
  PayloadPersonalProfileEkyc,
} from '../../../model/type/account';
import BasicInfo from './components/basic-info';
import {Color} from '../../../constants';

const InformationScreen = () => {
  const {t} = useTranslation();
  const [showModal, setShowModal] = useState('');
  const {dataAccount} = useAppSelector(state => state.account);
  const [avatarUrl, setAvatarUrl] = useState('');
  const [isPersonalAccount, setIsPersonalAccount] = useState<any>();
  const [orgDataAuthenticated, setOrgDataAuthenticated] =
    useState<OrgProfileAuthenticated>();
  const [authorityReceivedPersonal, setAuthorityReceivedPersonal] =
    useState<OrgLegalRepresentative>();
  const [personalDataAuthenticated, setPersonalDataAuthenticated] =
    useState<PayloadPersonalProfileEkyc>();

  const [isLoading, setIsLoading] = useState(false);
  const AccountServices = useAccountInfoServices();

  useEffect(() => {
    intiDataInfo();
  }, []);

  const intiDataInfo = async () => {
    try {
      const checkPerson =
        dataAccount.custType === AccountSystem.CUSTOMER
          ? true
          : !dataAccount?.authorities?.includes('ROLE_ORG_ADMIN') ||
            dataAccount.email !== dataAccount.emailCustomer;
      setIsPersonalAccount(checkPerson);

      await Promise.all([getAvatar(), handleGetDataInfo(checkPerson)]);
    } catch (error) {
    } finally {
    }
  };

  const handleGetDataInfo = async (isPersonalAccount: boolean) => {
    try {
      setIsLoading(true);
      if (isPersonalAccount && dataAccount.existedIdCard && dataAccount.id) {
        await getPersonalProfileEkyc();
      }

      if (!isPersonalAccount) {
        if (dataAccount.step === '2' && dataAccount.legalType === '1') {
          await getDetailOrgin();
        }
        if (dataAccount.step === '4' && dataAccount.legalType === '2') {
          await Promise.all([getOrgLegalInProfileAuth(), getDetailOrgin()]);
        }
      }
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  const getDetailOrgin = async () => {
    const dataResp = await AccountServices.getOrganizationDetail();
    if (dataResp) {
      setOrgDataAuthenticated(dataResp);
    }
  };

  const getOrgLegalInProfileAuth = async () => {
    const dataResp = await AccountServices.getOrgLegalInProfileAuth('3');
    if (dataResp) {
      setAuthorityReceivedPersonal(dataResp);
    }
  };

  const getPersonalProfileEkyc = async () => {
    const dataResp = await AccountServices.getPersonalProfileEkyc(
      dataAccount?.id,
    );
    if (dataResp) {
      setPersonalDataAuthenticated(dataResp);
    }
  };

  const getAvatar = async () => {
    try {
      const dataResp = await CommonUtils.downloadFile(
        `avatar-${new Date().getTime()}.jpg`,
        `${getConfig().BASE_URL_EACCOUNT}/services/uaa/api/account/avatar`,
        false,
      );
      setAvatarUrl(dataResp.uri);
    } catch (error) {}
  };

  const onCloseModal = () => setShowModal('');

  const getRoleUserOrg = () => {
    if (!dataAccount?.organization) return;

    const roleData = dataAccount?.roles || [];

    return roleData?.map((item: {name: string}) => item.name)?.join(', ');
  };

  const renderContent = useCallback(() => {
    if (isLoading) {
      return <ActivityIndicator color={Color.BgPrimary} size={'large'} />;
    }

    let dataInfoBacis: any[] = [];
    let dataInfoAuthen: any = {};
    let dataOrgAndLegal: any = {};
    const keyOfPhone = {
      key: 'CM.labelPhone',
      type: 'normal',
      value: {
        name: dataAccount.phone || '---',
        withIcon: (
          <TouchableOpacity
            hitSlop={15}
            onPress={() => setShowModal('changePhone')}>
            <Icon
              fill={'#00204DB2'}
              name="edit-2-outline"
              width={20}
              height={20}
            />
          </TouchableOpacity>
        ),
      },
    };
    dataInfoBacis = [
      {
        key: 'Email',
        type: 'normal',
        value: {
          name: dataAccount.email || '---',
        },
      },
      keyOfPhone,
    ];

    // personal account in org
    if (isPersonalAccount && dataAccount?.organization) {
      dataInfoAuthen = {
        infoOrg: [
          {
            key: 'ST.homeInformation.department',
            type: 'normal',
            value: {
              name: dataAccount?.orgName || '---',
            },
          },
          {
            key: 'CM.orgIn',
            type: 'normal',
            value: {
              name: dataAccount?.parentOrgName || '---',
            },
          },
          {
            key: 'ST.homeInformation.title',
            type: 'normal',
            value: {
              name: getRoleUserOrg() || '---',
            },
          },
        ],
      };
    }
    // personal account
    if (isPersonalAccount) {
      dataInfoAuthen = {
        ...dataInfoAuthen,
        infoPersonal: [
          {
            key: 'RV.eid.name',
            type: 'normal',
            value: {
              name: personalDataAuthenticated?.personalName || '---',
            },
          },
          {
            key: 'ST.homeInformation.gender',
            type: 'normal',
            value: {
              name: personalDataAuthenticated?.ekycContent?.gioiTinh || '---',
            },
          },
          {
            key: 'ST.homeInformation.dateOfBirth',
            type: 'normal',
            value: {
              name: personalDataAuthenticated?.ekycContent?.namSinh || '---',
            },
          },
          {
            key: 'Email',
            type: 'normal',
            value: {
              name: personalDataAuthenticated?.email || '---',
            },
          },
          {
            key: 'ST.homeInformation.taxCode',
            type: 'normal',
            value: {
              name: personalDataAuthenticated?.taxID || '---',
            },
          },
          keyOfPhone,
        ],
      };
    }

    // org, legal
    if (!isPersonalAccount) {
      dataOrgAndLegal = {
        org: [
          {
            key: 'ST.homeInformation.companyName',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.organization?.name || '---',
            },
          },
          {
            key: 'ST.homeInformation.taxCode',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.organization?.taxCode || '---',
            },
          },
          {
            key: 'ST.homeInformation.career',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.organization?.mainJob || '---',
            },
          },
          {
            key: 'ST.homeInformation.companyAddress',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.organization?.address || '---',
            },
          },
          {
            key: 'Email',
            type: 'normal',
            value: {
              name: dataAccount?.email || '---',
            },
          },
          keyOfPhone,
        ],
        legal: [
          {
            key: 'RV.eid.name',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.legal?.name || '---',
            },
          },
          {
            key: 'ST.homeInformation.gender',
            type: 'normal',
            value: {
              name: orgDataAuthenticated?.legal?.gender || '---',
            },
          },
          {
            key: 'ST.homeInformation.dateOfBirth',
            type: 'normal',
            value: {
              name:
                DateUtils.convertDateToDDMMYYYY(
                  orgDataAuthenticated?.legal?.dob as any,
                ) || '---',
            },
          },
        ],
      };
    }

    if (isPersonalAccount) {
      if (!personalDataAuthenticated) {
        return (
          <>
            <BasicInfo title="ST.information" dataInfo={dataInfoBacis} />
            {dataAccount.organization && (
              <BasicInfo
                title="ST.homeInformation.staffInformation"
                dataInfo={dataInfoAuthen['infoOrg']}
              />
            )}
          </>
        );
      } else {
        return (
          <>
            <>
              {dataInfoAuthen?.['infoOrg'] && (
                <BasicInfo
                  title="ST.homeInformation.staffInformation"
                  dataInfo={dataInfoAuthen?.['infoOrg']}
                />
              )}
              {dataInfoAuthen?.['infoPersonal'] && (
                <BasicInfo
                  title="ST.information"
                  dataInfo={dataInfoAuthen?.['infoPersonal']}
                />
              )}
            </>
          </>
        );
      }
    }
    if (!isPersonalAccount) {
      if (!orgDataAuthenticated) {
        return <BasicInfo title="ST.information" dataInfo={dataInfoBacis} />;
      } else {
        return (
          <>
            {dataOrgAndLegal?.['org'] && (
              <BasicInfo
                title="ST.homeInformation.companyInformation"
                dataInfo={dataOrgAndLegal?.['org']}
              />
            )}
            {dataOrgAndLegal?.['legal'] && (
              <BasicInfo
                title="ST.homeInformation.representativeInformation"
                dataInfo={dataOrgAndLegal?.['legal']}
              />
            )}
          </>
        );
      }
    }
  }, [
    isPersonalAccount,
    personalDataAuthenticated,
    orgDataAuthenticated,
    isLoading,
  ]);

  const getNameOrgin = () => {
    return isPersonalAccount
      ? dataAccount?.organization
        ? 'CM.employeeProfile'
        : 'CM.personalProfile'
      : 'CM.businessProfile';
  };

  return (
    <SafeAreaView style={styles.ctnMain} edges={['bottom']}>
      <HeaderCM
        style={{
          backgroundColor: '#6F2CED',
        }}
        contentStyle={{
          backgroundColor: '#6F2CED',
        }}
        title={t('ST.TxtAccountInformation')}
      />

      <View style={{marginBottom: 30}}>
        <View style={styles.ctnheader}>
          <View
            style={CommonUtils.generateBoxShadowStyle(
              0,
              4,
              '#1A3C5412',
              5,
              3,
              5,
              '#1A3C5412',
            )}>
            <View style={styles.ctnView}>
              <View style={styles.ctnOrg}>
                <View style={{flex: 1}} />
                <View style={styles.ctnOrgView}>
                  <TextCM style={styles.textOrg}>{t(getNameOrgin())}</TextCM>
                </View>
              </View>
              <View style={styles.ctnInfo}>
                <TouchableOpacity
                  onPress={() => setShowModal('avatar')}
                  style={{
                    position: 'relative',
                  }}>
                  <Image
                    width={50}
                    height={50}
                    borderRadius={25}
                    source={{
                      uri:
                        avatarUrl ||
                        'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                    }}
                  />
                  <View style={{position: 'absolute', bottom: 0, right: 0}}>
                    <IconCamera width={16} height={16} />
                  </View>
                </TouchableOpacity>
                <View style={{flex: 1}}>
                  <TextCM style={styles.textname}>
                    {'' + dataAccount.firstName + ' ' + dataAccount.lastName}
                  </TextCM>
                  <TextCM style={styles.textEmail}>
                    {dataAccount.email || '---'}
                  </TextCM>
                </View>
              </View>
            </View>
          </View>
        </View>
      </View>

      <ScrollView style={styles.ctnMainIfo}>{renderContent()}</ScrollView>

      <BottomSheetCM
        isVisible={showModal === 'avatar'}
        title={t('ST.changeAvatar')}
        onClose={onCloseModal}
        renderContent={
          <AvatarModal avatarUrl={avatarUrl} setAvatar={setAvatarUrl} />
        }
      />

      <ModalFullScreen
        isVisible={showModal === 'changePhone'}
        children={<ChangePhone onClose={onCloseModal} />}
      />
    </SafeAreaView>
  );
};

export default InformationScreen;
