import {ScrollView, View} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../../components/Header/HeaderCM';
import {useTranslation} from 'react-i18next';
import {useAccountSettingService} from '../../../../hooks/apiHooks/useAccountInfoServices';
import {useEffect, useState} from 'react';
import {
  IConfigPassword,
  IPasswordConfig,
  isValueInEnum,
  LIST_CONFIG_PASSWORD,
} from './interface';
import ConfigItem from './components/ConfigItem';
import ModalConfigPassword from './components/ModalConfigPassword';
import Toast from 'react-native-toast-message';
const PasswordSetting = () => {
  const {t} = useTranslation();
  const [showModalConfig, setShowModalConfig] = useState(false);
  const [data, setData] = useState<IPasswordConfig[]>([]);
  const [current, setCurrent] = useState<any>();
  const AccountSettingServices = useAccountSettingService();

  const toggleModalConfig = () => {
    setShowModalConfig(!showModalConfig);
  };

  useEffect(() => {
    getAllConfig();
  }, []);

  const getAllConfig = async () => {
    const dataConfig = await AccountSettingServices.getAllConfig();
    if (dataConfig) {
      const filterData = dataConfig.filter(
        (item: IPasswordConfig) =>
          item.type === 'password' &&
          item.custId !== 0 &&
          isValueInEnum(item.configType, IConfigPassword),
      );
      if (filterData.length > 0) {
        setData(filterData);
      } else {
        setData(
          dataConfig.filter(
            (item: IPasswordConfig) =>
              item.type === 'password' &&
              item.custId === 0 &&
              isValueInEnum(item.configType, IConfigPassword),
          ),
        );
      }
    }
  };

  const onPressEdit = (item: IPasswordConfig) => () => {
    toggleModalConfig();
    setCurrent(item);
  };

  const onPressSwitch = (item: IPasswordConfig) => async () => {
    try {
      const findIndex = data.findIndex(i => i.configType === item.configType);
      data[findIndex].status = !item.status;
      const res = await AccountSettingServices.editSecurityPasswordConfig(data);
      if (res) {
        setData(res);
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('GS.configSuccess'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.error'),
      });
    }
  };

  const onSubmitEdit = async (type: IConfigPassword, editAttr: any) => {
    try {
      const res = await AccountSettingServices.editSecurityPasswordConfig(
        data.map(item => {
          if (item.configType === type) {
            return {
              ...item,
              ...editAttr,
            };
          }
          return item;
        }),
      );
      if (res) {
        setData(res);
        toggleModalConfig();
        Toast.show({
          type: 'success',
          text1: t('CM.noti'),
          text2: t('GS.configSuccess'),
        });
      }
    } catch (error) {}
  };

  return (
    <SafeAreaView style={{flex: 1}} edges={['bottom']}>
      <HeaderCM
        style={{
          backgroundColor: '#6F2CED',
        }}
        contentStyle={{
          backgroundColor: '#6F2CED',
        }}
        title={t('Ci t mt khu')}
      />
      <ScrollView contentContainerStyle={{flex: 1, rowGap: 16}}>
        {data.map(item => {
          return (
            <ConfigItem
              data={item}
              key={item.configType}
              onPress={onPressEdit(item)}
              onPressSwitch={onPressSwitch(item)}
              label={
                LIST_CONFIG_PASSWORD.find(i => i.type === item.configType)
                  ?.label ?? ''
              }
            />
          );
        })}
      </ScrollView>

      {showModalConfig && (
        <ModalConfigPassword
          type={current?.configType}
          onClose={toggleModalConfig}
          isVisible={showModalConfig}
          data={current}
          onSubmit={onSubmitEdit}
        />
      )}
    </SafeAreaView>
  );
};

export default PasswordSetting;
