import React from 'react';
import {useTranslation} from 'react-i18next';
import {TouchableOpacity, View} from 'react-native';
import styles from './styles';
import {IPasswordConfig} from '../../interface';
import TextCM from '../../../../../components/Text';
import {Icon, Toggle} from '@ui-kitten/components';

interface Props {
  onPress: () => void;
  onPressSwitch: () => void;
  label: string;
  data: IPasswordConfig;
}

const ConfigItem = ({onPress, label, onPressSwitch, data}: Props) => {
  const {t} = useTranslation();

  return (
    <TouchableOpacity>
      <View style={styles.item}>
        <TextCM style={styles.txtName}>{t(label)}</TextCM>
        <TextCM style={styles.txtEdit}>{t('GS.shareConfig.edited')}</TextCM>
        <View style={styles.viewAction}>
          <Toggle
            status="basic"
            checked={data.status}
            onChange={onPressSwitch}
          />
          <TouchableOpacity style={styles.btnEdit} onPress={onPress}>
            <Icon name="edit-2-outline" fill={'blue'} width={14} height={14} />
          </TouchableOpacity>
        </View>
      </View>
    </TouchableOpacity>
  );
};

export default ConfigItem;
