import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../components/Header/HeaderCM';
import {Alert, ScrollView, TouchableOpacity, View} from 'react-native';
import {
  Icon,
  IndexPath,
  MenuItem,
  OverflowMenu,
  Toggle,
} from '@ui-kitten/components';
import TextCM from '../components/Text';
import styles from './styles';
import {ListInfo, ListLanguages, TypeSetting} from './helper';
import {useEffect, useState} from 'react';
import {useAppDispatch, useAppSelector} from '../../hooks/useRedux';
import {commonActions} from '../../redux/slices';
import {useTranslation} from 'react-i18next';
import {useNavigation, useRoute} from '@react-navigation/native';
import NavigationNameAccount from '../../constants/NavigationNameAccount';
import {useTicketServices} from '../../hooks/apiHooks/useTicketServices';
import {useAccountInfoServices} from '../../hooks/apiHooks/useAccountInfoServices';
import ModalConfirmCM from '../components/ModalConfirm';
import React from 'react';
import {useZutandStore} from '../../zutand/store';
import {PermissionUtils} from '../../utils';

const AccountScreen = () => {
  const {listMenuViewEAccount} = useZutandStore();
  const navigation = useNavigation<any>();
  const router = useRoute<any>();
  const {handleRegisterNotification, handleUnRegisterNotification} =
    router.params;

  const {t, i18n} = useTranslation();
  const dispatch = useAppDispatch();
  const {language, activeRegisterNotify} = useAppSelector(
    state => state.common,
  );
  const {dataAccount} = useAppSelector(state => state.account);
  const [visible, setVisible] = useState(false);
  const [modalReset, setModalReset] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState<IndexPath>(
    new IndexPath(
      language ? ListLanguages.findIndex(lang => lang.value === language) : 0,
    ),
  );
  const [isShowData, setIsShowData] = useState<{[key: string]: boolean}>({});
  const ListServices = useTicketServices();
  const AccountServices = useAccountInfoServices();

  const toggleModalReset = () => setModalReset(!modalReset);

  useEffect(() => {
    i18n.changeLanguage(language);
    // login();
  }, []);

  const login = async () => {
    await ListServices.login();
  };

  const toggleCheckNoti = () => {
    activeRegisterNotify
      ? handleUnRegisterNotification?.()
      : handleRegisterNotification?.();
  };

  const onItemSelect = async (index: IndexPath) => {
    const valueSelect = ListLanguages[index?.row].value;
    const dataUpdate = await AccountServices.updateAccount({
      ...dataAccount,
      langKey: valueSelect,
    });
    if (dataUpdate) {
      setSelectedIndex(index);
      i18n.changeLanguage(valueSelect);
      dispatch(commonActions.setLanguage(valueSelect));
    } else {
      Alert.alert(t('CM.forwardFail'));
    }
    setVisible(false);
  };

  const getValueSelect = () => {
    return ListLanguages[selectedIndex?.row]?.icon;
  };

  const handleLogout = () => {
    dispatch(commonActions.setIsAuthorized(false));
    dispatch(commonActions.setAccessToken(''));
  };

  const handleAction = (action: string) => {
    switch (action) {
      case 'info':
        navigation.navigate(NavigationNameAccount.InformationScreen);
        break;
      case 'changePassword':
        navigation.navigate(NavigationNameAccount.ChangePasswordScreen);
        break;
      case 'language':
        setVisible(!visible);
        break;
      case 'logout':
        handleLogout();
        break;
      case 'reset':
        toggleModalReset();
        break;
      case 'showListSettings':
        setIsShowData(prev => ({
          ...prev,
          [action]: !prev[action],
        }));
        break;
      case 'delete':
        Alert.alert(
          t('CM.noti'),
          'Vui lng lin h customersupport@fpt.com.vn  c h tr',
        );
        break;
      default:
        break;
    }
  };

  const handleResetAccount = () => {
    if (activeRegisterNotify) {
      handleUnRegisterNotification?.();
    }
    dispatch(commonActions.setIsSaveAccount(false));
    toggleModalReset();
  };

  const renderOverFlowMenuIcon = () => {
    return (
      <OverflowMenu
        anchor={() => (
          <TouchableOpacity onPress={() => setVisible(true)}>
            {getValueSelect()}
          </TouchableOpacity>
        )}
        visible={visible}
        selectedIndex={selectedIndex}
        onSelect={onItemSelect}
        onBackdropPress={() => setVisible(false)}>
        {ListLanguages.map(languae => (
          <MenuItem title={t(languae.title)} accessoryRight={languae.icon} />
        ))}
      </OverflowMenu>
    );
  };

  const renderDropdownList = (action: any) => {
    return (
      <View style={{marginLeft: 20}}>
        {isShowData?.[action?.action] &&
          action?.data?.map((item: any) => (
            <MenuItem
              onPress={() => handleSelectSettings(item.type)}
              title={item.name}
            />
          ))}
      </View>
    );
  };

  const handleSelectSettings = (type: TypeSetting) => {
    switch (type) {
      case 'settingPassword':
        navigation.navigate(NavigationNameAccount.PasswordSetting);
        break;
      case 'settingLogin':
        break;
      case 'settingIP':
        break;
      default:
        break;
    }
  };

  return (
    <SafeAreaView style={{flex: 1}} edges={['bottom']}>
      <HeaderCM
        style={{
          backgroundColor: '#6F2CED',
        }}
        contentStyle={{
          backgroundColor: '#6F2CED',
        }}
        title={t('ST.Settings')}
      />
      <ScrollView style={styles.ctnMain}>
        {ListInfo.map(
          action =>
            PermissionUtils.hasPermission(
              listMenuViewEAccount,
              action.permission,
            ) && (
              <>
                <TouchableOpacity
                  onPress={() => handleAction(action.action)}
                  key={action.title}
                  style={styles.ctnBody}>
                  <View style={styles.ctnLeft}>
                    {action.icon}
                    <TextCM style={styles.textTitle}>{t(action.title)}</TextCM>
                  </View>
                  {(action.type === 'next' || action.type === 'dropdown') && (
                    <View
                      style={[
                        isShowData[action.action] && {
                          transform: [{rotate: '90deg'}],
                        },
                      ]}>
                      <Icon
                        width={20}
                        height={20}
                        name="arrow-ios-forward-outline"
                      />
                    </View>
                  )}

                  {action.type === 'switch' && (
                    <Toggle
                      checked={activeRegisterNotify}
                      onChange={toggleCheckNoti}
                      status="basic"
                    />
                  )}

                  {action.type === 'list' && renderOverFlowMenuIcon()}
                </TouchableOpacity>
                {action.type === 'dropdown' && renderDropdownList(action)}
              </>
            ),
        )}
      </ScrollView>

      <ModalConfirmCM
        type="cancel"
        title={t('CM.noti')}
        content="Khi khi phc li ti khon th bn c th thay i ti khon v s khng nhn c thng bo vi ti khon ny na"
        onClose={toggleModalReset}
        isVisible={modalReset}
        labelCancel={t('CM.back')}
        labelConfirm={t('CM.confirm')}
        onConfirm={handleResetAccount}
      />
    </SafeAreaView>
  );
};

export default AccountScreen;
